
.cart-list {
  background-color: #eee;
}
.item-ch {
  width: 26px;
  display: inline-block;
  margin-right: 10px;
  height: 26px;
  background-color: #ccc;
  border-radius: 50%;
  position: relative;
}
.item-ch:after {
  content: '';
  background-color: #eee;
  width: 20px;
  height: 20px;
  position: absolute;
  left: 3px;
  top: 3px;
  border-radius: 50%;
}
.selected:after {
  background-color: #f71;
}
.cart-item {
  margin-bottom: 10px;
  background-color: #fff;
  overflow: hidden;
  padding: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.cart-item img {
    width: 100px;
    height: 100px;
}
.cart-item .info {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding-left: 10px;
}
.cart-item button {
    padding: 5px 10px;
}



